import LaptopScreen from "../LaptopScreen/LaptopScreen";
import Macbook from "../Models/Macbook";
import Name from "../Name";
import PhoneScreen from "../PhoneScreen/PhoneScreen";
import PortfolioControl, { FocusPoint } from "../PortfolioControl";
import StickyNotes from "../StickyNotes";
import { useContext } from "react";
import { FocusDeviceContext } from "../providers/FocusDeviceProvider";

const laptopFocusPoint: FocusPoint = {
  target: [-0.239, 0.146, -0.386],
  azimuthAngle: 0.033,
  polarAngle: 1.313,
  minDistance: 1.8,

  // for debugging set this to true
  // enabled: true,
};

const Interactables = () => {
  const { isMacbookOn, setIsMacbookOn, setFocusPoint } =
    useContext(FocusDeviceContext);

  return (
    <>
      <PortfolioControl />
      <Macbook
        castShadow
        isInteractionEnabled={!isMacbookOn}
        position-y={-1.5}
      />
      <LaptopScreen
        isLaptopOn={isMacbookOn}
        setIsLaptopOn={(isLaptopOn) => {
          setIsMacbookOn(isLaptopOn);
          setFocusPoint(isLaptopOn ? laptopFocusPoint : undefined);
        }}
      />

      <PhoneScreen />
      <StickyNotes />
      <Name />
    </>
  );
};

export default Interactables;
