import { OrbitControls, OrbitControlsProps } from "@react-three/drei";
import { clamp } from "three/src/math/MathUtils.js";
import { useContext, useEffect, useRef } from "react";
import { OrbitControls as OrbitControlsImpl } from "three-stdlib";
import {
  FocusDeviceContext,
  FocusDeviceContextType,
} from "./providers/FocusDeviceProvider";

export type FocusPoint = {
  target: [x: number, y: number, z: number];
  azimuthAngle: number;
  polarAngle: number;
  minDistance: number;

  /**
   * if empty or undefined, it will be set to `minDistance + 0.2`
   */
  maxDistance?: number;
  /**
   * Rotation, pan, and zoom speed
   * @default 2
   */
  speed?: number;
  /**
   * whether to enable controls
   * @default false
   */
  enabled?: boolean;
};

const defaultFocusPoint: FocusPoint & {
  maxDistance: number;
  speed: number;
} = {
  target: [-0.8, 0.2, -0.5],
  azimuthAngle: 0.6,
  polarAngle: 1.24,
  minDistance: 2,
  maxDistance: 7,
  speed: 0.5,
  enabled: true,
};

type PortfolioControlProps = OrbitControlsProps;

export default function PortfolioControl({ ...props }: PortfolioControlProps) {
  const { focusPoint } = useContext<FocusDeviceContextType>(FocusDeviceContext);

  const setFocusPoint = (focus?: FocusPoint) => {
    if (!controlsRef.current) {
      return;
    }

    const focusPoint = focus ?? defaultFocusPoint;
    focusPoint.speed = focusPoint.speed ?? 2;
    focusPoint.maxDistance =
      focusPoint.maxDistance ?? focusPoint.minDistance + 0.2;

    controlsRef.current.enablePan = !!focusPoint?.enabled;
    controlsRef.current.enableZoom = !!focusPoint?.enabled;
    controlsRef.current.enableRotate = !!focusPoint?.enabled;
    controlsRef.current.target.set(...focusPoint.target);
    controlsRef.current.setAzimuthalAngle(focusPoint.azimuthAngle);
    controlsRef.current.setPolarAngle(focusPoint.polarAngle);
    controlsRef.current.rotateSpeed = focusPoint.speed;
    controlsRef.current.panSpeed = focusPoint.speed;
    controlsRef.current.zoomSpeed = focusPoint.speed;
    controlsRef.current.maxDistance = focusPoint.maxDistance;
    controlsRef.current.minDistance = focusPoint.minDistance;
  };

  useEffect(() => {
    if (!controlsRef.current) {
      return;
    }

    if (focusPoint) {
      setFocusPoint(focusPoint);
    } else {
      // set camera to furthest point
      // before setting the default focus point
      controlsRef.current.minDistance = defaultFocusPoint.maxDistance;
      setFocusPoint();
    }
  }, [focusPoint]);

  const controlsRef = useRef<OrbitControlsImpl>(null);

  return (
    <>
      <OrbitControls
        ref={controlsRef}
        makeDefault
        minAzimuthAngle={-Math.PI / 4}
        maxAzimuthAngle={Math.PI / 2}
        minPolarAngle={Math.PI / 4}
        maxPolarAngle={Math.PI / 2}
        dampingFactor={0.1}
        enablePan={true}
        enableZoom={true}
        maxDistance={defaultFocusPoint.maxDistance}
        minDistance={defaultFocusPoint.minDistance}
        rotateSpeed={defaultFocusPoint.speed}
        panSpeed={defaultFocusPoint.speed}
        zoomSpeed={defaultFocusPoint.speed}
        // to debug camera position
        // onEnd={() => {
        //   console.log({
        //     target: controlsRef.current?.target,
        //     azimuth: controlsRef.current?.getAzimuthalAngle(),
        //     polar: controlsRef.current?.getPolarAngle(),
        //     zoom: controlsRef.current?.getDistance(),
        //     a: controlsRef.current?.dampingFactor,
        //   });
        // }}
        onChange={
          // limit panning
          () => {
            const target = controlsRef.current?.target;

            if (!target) {
              return;
            }

            const targetX = clamp(target.x, -1, 1);
            const targetY = clamp(target.y, -1, 1);
            const targetZ = clamp(target.z, -1, 1);

            target.setX(targetX);
            target.setY(targetY);
            target.setZ(targetZ);
          }
        }
        {...props}
      />
    </>
  );
}
