import { ContactShadows, Environment, SpotLight } from "@react-three/drei";
import FocusDeviceProvider from "./providers/FocusDeviceProvider";
import Interactables from "./Interactables/Interactables";
import { Perf } from "r3f-perf";
import { isProduction } from "./utils/isProduction";
import Particles from "./Particles/Particles";

export default function Experience() {
  const isDevelopment = !isProduction();

  return (
    <>
      {isDevelopment && (
        <Perf openByDefault trackGPU={true} position="top-right" />
      )}
      <Environment preset="apartment" />
      <color args={["#1B2128"]} attach="background" />
      <SpotLight
        position={[-4, 5, 0]}
        angle={0.25}
        distance={7}
        attenuation={7}
        radiusTop={0.8}
        radiusBottom={2}
        color={"#FFF5B6"}
        penumbra={0.5}
        opacity={0.3}
        anglePower={5}
      />
      <ambientLight intensity={1} color={"#FFF5B6"} />

      <Particles />

      <FocusDeviceProvider>
        <Interactables />
      </FocusDeviceProvider>

      <ContactShadows
        position-y={-1}
        opacity={0.5}
        scale={6}
        blur={7}
        far={2}
        frames={1}
      />
    </>
  );
}
