import { Text } from "@react-three/drei";
import React from "react";
import { animated, easings, useSpring } from "@react-spring/three";
import pointerEvents from "./utils/pointerEvents";
import { FONTS } from "./assets";

export default function StickyNotes() {
  const [isSocialMediaNoteRevealed, setIsSocialMediaNoteRevealed] =
    React.useState(false);

  const [spring, springApi] = useSpring(() => ({
    position: [2.26, -1.04, 0.6],

    config: {
      mass: 1,
      tension: 170,
      friction: 26,
      duration: 500,
      easing: easings.easeOutSine,
    },
  }));

  const handleSocialMediaNoteClick = () => {
    if (isSocialMediaNoteRevealed) {
      springApi.start({
        position: [2.93, -1.03, 0.7],
      });
    } else {
      springApi.start({
        position: [2.26, -1.03, 0.6],
      });
    }

    setIsSocialMediaNoteRevealed(!isSocialMediaNoteRevealed);
  };

  return (
    <>
      <group position={[1.25, -0.98, 0.7]} rotation-y={-0.2}>
        <mesh scale={[0.7, 0.01, 0.8]}>
          <boxGeometry />
          <meshBasicMaterial color={"#C0B375"} />
        </mesh>
        <Text
          font={FONTS.INDIE_FLOWER_TTF}
          fontSize={0.08}
          position={[0.04, 0.01, 0]}
          rotation={[-Math.PI / 2, 0, 0]}
          color={"#393533"}
          maxWidth={0.6}
        >
          Turn off the laptop after use. It's that red button on the right.
        </Text>
      </group>

      <animated.group
        position={spring.position.to((x, y, z) => [x, y, z])}
        rotation-y={0.3}
        onClick={handleSocialMediaNoteClick}
        {...pointerEvents}
      >
        <mesh scale={[0.7, 0.01, 0.8]}>
          <boxGeometry />
          <meshBasicMaterial color={"#C0B375"} />
        </mesh>
        <Text
          font={FONTS.INDIE_FLOWER_TTF}
          fontSize={0.09}
          position={[0.02, 0.01, 0]}
          rotation={[-Math.PI / 2, 0, 0]}
          color={"#393533"}
          maxWidth={0.6}
        >
          Got a project in mind? Iâ€™m all ears and ready to connect.
        </Text>
      </animated.group>
    </>
  );
}
