import { animated, config, useSpring } from "@react-spring/three";
import { Text3D, useMatcapTexture } from "@react-three/drei";
import React, { Suspense, useEffect } from "react";
import pointerEvents from "./utils/pointerEvents";
import { FONTS } from "./assets";

export default function Name() {
  const matcapTexture = useMatcapTexture("6D6D6D_3E3E3E_C0C0C0_949494", 256);
  const lightMatCapTexture = useMatcapTexture(
    "6EC66E_C9F8C9_A3E8A3_B4F1B4",
    256
  );
  const lightSwitchSound = new Audio("sfx/light-switch.m4a");
  const [isFirstNameOn, setIsFirstNameOn] = React.useState(false);
  const [isLastNameOn, setIsLastNameOn] = React.useState(false);

  const [spring, api] = useSpring(() => ({
    firstNameMatcap: matcapTexture[0],
    lastNameMatCap: matcapTexture[0],
    config: config.wobbly,
  }));

  useEffect(() => {
    setTimeout(() => {
      const firstNameCheckInterval = setInterval(() => {
        api.start({
          firstNameMatcap: lightMatCapTexture[0],
        });
        api.start({
          lastNameMatCap: matcapTexture[0],
        });
      }, 500);

      const lastNameCheckInterval = setInterval(() => {
        api.start({
          firstNameMatcap: matcapTexture[0],
        });
        api.start({
          lastNameMatCap: lightMatCapTexture[0],
        });
      }, 1000);

      setTimeout(() => {
        api.start({
          lastNameMatCap: matcapTexture[0],
        });
        clearInterval(firstNameCheckInterval);
        clearInterval(lastNameCheckInterval);
      }, 2400);
    }, 3 * 1000);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Suspense>
      <Text3D
        font={FONTS.SILKSCREEN_JSON}
        size={0.4}
        height={0.1}
        position={[-1.7, -0.9, 0.8]}
        rotation-x={-0}
        rotation-y={1.6}
        curveSegments={12}
        bevelEnabled
        bevelSize={0.01}
        bevelThickness={0.01}
        letterSpacing={-0.05}
        onClick={(e) => {
          e.stopPropagation();
          lightSwitchSound.play();
          api.start({
            firstNameMatcap: isFirstNameOn
              ? matcapTexture[0]
              : lightMatCapTexture[0],
          });
          setIsFirstNameOn(!isFirstNameOn);
        }}
        {...pointerEvents}
      >
        Rizki
        <animated.meshMatcapMaterial matcap={spring.firstNameMatcap} />
      </Text3D>
      {/* <rectAreaLight
        width={2}
        height={1.5}
        intensity={30}
        color={"#BECFA5"}
        rotation={[0, -Math.PI / 2, 0]}
        position={[-1.8, -1.4, 0]}
      /> */}

      <Text3D
        font={FONTS.SILKSCREEN_JSON}
        size={0.32}
        height={0.07}
        position={[-1.45, -1.04, 1.5]}
        rotation-x={-Math.PI / 2}
        rotation-y={0}
        curveSegments={12}
        bevelEnabled
        bevelSize={0.01}
        bevelThickness={0.01}
        letterSpacing={-0.05}
        onClick={(e) => {
          e.stopPropagation();
          lightSwitchSound.play();
          api.start({
            lastNameMatCap: isLastNameOn
              ? matcapTexture[0]
              : lightMatCapTexture[0],
          });
          setIsLastNameOn(!isLastNameOn);
        }}
        {...pointerEvents}
      >
        Fikriansyah
        <animated.meshMatcapMaterial matcap={spring.lastNameMatCap} />
      </Text3D>
    </Suspense>
  );
}
